* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

#panel1{
    width: 100%;
    height: 40px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
    background-color: #7c8b89; 
    color: rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;  
  
}


#panel2{
    width: 100%;
    height: 190px;
    border-top: #ebe7e7 1px solid;
    background-color:#29313b;
    display: flex;
    flex-direction: row;
    justify-content: center;


}


#panel2items{
    width: 200px;
    height: 150px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    
    margin-left: 30px;
    margin-right: 30px;
    margin-top:30px;
    
}


#d1{
    color: white; 
    text-decoration: none;
    list-style-type: none;
    font-size: 12px;
    margin-top: 30px;
    

}

#d1:hover{
    text-decoration: underline;
}
#title{
    color: rgb(179, 235, 220);
    font-size: 15px;
    font-weight:900;
}

#footer2{
    padding:10px;
    height: 110px;
    width: 100%;
    background-color:#1f2c3b; 
    border-top: 1px solid white;
    color: white;

}
.horizontal{
    margin-top: 10px;
    width: 100%;
    border-top:2px solid #7c8b89;
}
#vinay{
    margin-top: 10px;
    font-style: italic;
    font-weight: 900;
    font-family: 'Times New Roman', Times, serif;
}


#icon{
    font-size: 20px;

    margin-left: 40px;
    margin-right: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-right: 12px;
    display: inline;
     
}





